body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
 
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

/* sets up master color scheme */
:root {
  --mainBlue: #2a2a72;
  --lightBlue: #009ffd;
  --mainWhite: #f3f3f3;
  --mainDark: #232528;
  --mainYellow: #ffa400;
}

body {
  font-family: 'Oswald', sans-serif !important;
  background-image: url(/static/media/90s_Theme_Background.1b160bc2.png) !important;

  background-color: #cccc; 
  color: #232528 !important; 
  color: var(--mainDark) !important;
}

.text-title {
font-family: 'Permanent Marker', cursive;
letter-spacing: 0.3rem;
text-transform: uppercase;

}

.text-blue {
  color: #2a2a72;
  color: var(--mainBlue);

}

.text-bright {
  color: #009ffd;
  color: var(--lightBlue);
}

.btn-black {
  background: transparent;
  text-transform: capitalize;
  font-size: 0.8rem !important;
  color: #232528;
  color: var(--mainDark);
  border-radius: 0 !important;
  border: 0.1rem solid #232528 !important;
  border: 0.1rem solid var(--mainDark) !important;
}

.btn-black:hover {
  background: #232528 !important;
  background: var(--mainDark) !important;
  color: #f3f3f3 !important;
  color: var(--mainWhite) !important;
}

.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: #ffa400;
  color: var(--mainYellow);
}

.trash-icon {
  cursor: pointer;
  font-size: 2rem;
}




.deadInsideGif {
    margin-left: 10rem;
    margin-top: 10rem;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: repeat-y;
}

.defaultLandPage {
    background: url(/static/media/Dead_Inside.df4efa9a.gif) !important;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: repeat-y;
}
.titlecontainer {
    text-align: center;
    /* position: absolute; */
    top: 50%;
    left: 50%;
    margin-top: 10rem;
    /* margin-left: -50px; */
    color: var(--mainDark);
}
.titlecontainer h1 {
    font-weight: bold;
}
.titlecontainer h3 {
    font-weight: bold;
}

.homePageButton {
    background-color: white;
    border: none;
    color: grey;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    margin: 5rem 2rem;
    border-radius: 4px;
}

.homePageButton:hover {
    background-color: var(--mainBlue);
    border: none;
    color: var(--mainWhite);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    margin: 5rem 2rem;
    border-radius: 4px;
}
.SignupPage {
    text-align: center;
}
.LoginPage {
    text-align: center;
}

.form {
    text-align: center;
    padding-left: 40%;
}

.form-horizontal {
    padding-left: 40%;
}

.inputContainer {
    text-align: center;
}

#authbtn {
    background-color: var(--mainYellow) !important;
}

#authbtn:hover {
    background-color: var(--mainWhite) !important;
}



